import { useQuery, gql } from '@apollo/client';

// Minimal GraphQL hooks to satisfy tests

const BUSINESSES_QUERY = gql`
  query Businesses($limit: Int, $category: String) {
    businesses(limit: $limit, category: $category) {
      id
      name
      category
    }
  }
`;

const BUSINESS_DETAILS_QUERY = gql`
  query BusinessDetails($id: ID!) {
    business(id: $id) {
      id
      name
      description
      rating
    }
  }
`;

const NEARBY_BUSINESSES_QUERY = gql`
  query NearbyBusinesses($lat: Float!, $lng: Float!, $radius: Int) {
    nearbyBusinesses(lat: $lat, lng: $lng, radius: $radius) {
      id
      name
      distance
    }
  }
`;

export function useBusinessesQuery(variables?: { limit?: number; category?: string }) {
  return useQuery(BUSINESSES_QUERY, { variables });
}

export function useBusinessDetailsQuery(variables: { id: string }) {
  return useQuery(BUSINESS_DETAILS_QUERY, { variables, skip: !variables?.id });
}

export function useNearbyBusinessesQuery(variables: { lat: number; lng: number; radius?: number }) {
  return useQuery(NEARBY_BUSINESSES_QUERY, { variables, skip: !variables?.lat || !variables?.lng });
}
